import { p as pb } from "../../chunks/pocketbase.js";
const load = async () => {
  return {
    menus: await fetchMenus()
  };
};
async function fetchMenus() {
  const response = await pb.collection("menus").getList();
  const data = response.items.map((res) => {
    return {
      id: res.id,
      menuName: res.menuName,
      menuUrl: res.menuUrl
    };
  });
  return data;
}
export {
  load
};
